import { Box, Typography, Grid, Button } from "@mui/material";
import { Phone, Place, Schedule, Language } from "@mui/icons-material";
import { Asset } from "../../services/api/AssetService";

interface LandingPageTemplateProps {
  asset: Asset;
}

const LandingPageTemplate = ({ asset }: LandingPageTemplateProps) => {
  return (
    <Box sx={{ maxWidth: 1200, mx: "auto" }}>
      {/* Hero Section */}
      <Box
        sx={{
          height: 400,
          bgcolor: "primary.main",
          color: "white",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
          textAlign: "center",
          mb: 4,
        }}
      >
        <Typography variant="h2" component="h1" sx={{ mb: 2 }}>
          {asset.name}
        </Typography>
        <Typography variant="h5">{asset.formattedAddress}</Typography>
      </Box>

      {/* Info Section */}
      <Grid container spacing={4} sx={{ mb: 4 }}>
        {asset.formattedPhoneNumber && (
          <Grid item xs={12} sm={6} md={3}>
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              <Phone color="primary" />
              <Typography>{asset.formattedPhoneNumber}</Typography>
            </Box>
          </Grid>
        )}

        {asset.formattedAddress && (
          <Grid item xs={12} sm={6} md={3}>
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              <Place color="primary" />
              <Typography>{asset.formattedAddress}</Typography>
            </Box>
          </Grid>
        )}

        {asset.openingHours && (
          <Grid item xs={12} sm={6} md={3}>
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              <Schedule color="primary" />
              <Typography>
                {asset.openingHours.open_now ? "Open Now" : "Closed"}
              </Typography>
            </Box>
          </Grid>
        )}

        {asset.website && (
          <Grid item xs={12} sm={6} md={3}>
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              <Language color="primary" />
              <Typography
                component="a"
                href={asset.website}
                target="_blank"
                sx={{ color: "inherit", textDecoration: "none" }}
              >
                Visit Website
              </Typography>
            </Box>
          </Grid>
        )}
      </Grid>

      {/* About Section */}
      {asset.rating && (
        <Box sx={{ mb: 4 }}>
          <Typography variant="h4" sx={{ mb: 2 }}>
            About Us
          </Typography>
          <Typography variant="body1" sx={{ mb: 2 }}>
            Rating: {asset.rating} / 5
          </Typography>
          {asset.reviews?.slice(0, 3).map((review, index) => (
            <Box key={index} sx={{ mb: 2 }}>
              <Typography variant="body2" sx={{ fontStyle: "italic" }}>
                "{review.text}"
              </Typography>
              <Typography variant="caption" color="text.secondary">
                - {review.author}
              </Typography>
            </Box>
          ))}
        </Box>
      )}

      {/* Call to Action */}
      <Box sx={{ textAlign: "center", py: 4 }}>
        {asset.formattedPhoneNumber && (
          <Button
            variant="contained"
            size="large"
            href={`tel:${asset.formattedPhoneNumber}`}
            sx={{ mr: 2 }}
          >
            Call Now
          </Button>
        )}
        {asset.url && (
          <Button
            variant="outlined"
            size="large"
            href={asset.url}
            target="_blank"
          >
            View on Google Maps
          </Button>
        )}
      </Box>
    </Box>
  );
};

export default LandingPageTemplate;
